import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { catchError, tap, map } from 'rxjs/operators';

const httpOptions = {
	headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
const apiUrl = '/api';

@Injectable({
	providedIn: 'root'
})
export class ApiService {
	constructor(private http: HttpClient) {}

	private handleError(error: HttpErrorResponse) {
		if (error.error instanceof ErrorEvent) {
			// A client-side or network error occurred. Handle it accordingly.
			console.error('An error occurred:', error.error.message);
		} else {
			// The backend returned an unsuccessful response code.
			// The response body may contain clues as to what went wrong,
			console.error(`Backend returned code ${error.status}, ` + `body was: ${error.error}`);
		}
		// return an observable with a user-facing error message
		return throwError('Something bad happened; please try again later.');
	}

	private extractData(res: Response) {
		let body = res;
		return body || {};
	}

	getCases(): Observable<any> {
		return this.http.get(apiUrl, httpOptions).pipe(map(this.extractData), catchError(this.handleError));
	}
	getCase(id: string): Observable<any> {
		const url = `${apiUrl}/getCase/${id}`;
		return this.http.get(url, httpOptions).pipe(map(this.extractData), catchError(this.handleError));
	}
	postCase(data): Observable<any> {
		const url = `${apiUrl}/saveCase/`;
		return this.http.post(url, data, httpOptions).pipe(catchError(this.handleError));
	}
	updateCase(id: string, data): Observable<any> {
		const url = `${apiUrl}/updateCase/${id}`;
		return this.http.put(url, data, httpOptions).pipe(catchError(this.handleError));
	}
	deleteCase(id: string): Observable<{}> {
		const url = `${apiUrl}/deleteCase/${id}`;
		return this.http.delete(url, httpOptions).pipe(catchError(this.handleError));
	}
	getCountByMonth(): Observable<any> {
		const url = `${apiUrl}/getCountByMonth`;
		return this.http.get(url, httpOptions).pipe(map(this.extractData), catchError(this.handleError));
	}
	postTime(data): Observable<any> {
		const url = `${apiUrl}/sendTime/`;
		return this.http.post(url, data, httpOptions).pipe(catchError(this.handleError));
	}
	getTime(): Observable<any> {
		const url = `${apiUrl}/getTime`;
		return this.http.get(url, httpOptions).pipe(map(this.extractData), catchError(this.handleError));
	}
}
